@import 'src/shared/ui/variables.scss';
@import '../ProductCard.module.scss';

$card_width: calc($app-width / 2);

.root {
  width: $card_width;
  border-radius: 5px;
  @include theme(box-shadow, product_card_box_shadow);
  display: flex;
}

.product_image {
  @include product_image_style($card_width * 0.2);
  margin: 5px;
}

.text_continer {
  width: 100%;
  position: relative;
}

.title {
  @include title_style(16px);
  margin: 0px;
  padding: 6px;
  height: 1em;
}

.card_footer {
  position: absolute;
  width: 100%;
  bottom: 0%;
  display: flex;
  padding-bottom: 5px;
  align-items: center;
}

.sum {
  @include price_style(18px);
  float: left;
  margin-left: 6px;
  margin-right: auto;
}

.cart_button {
  float: right;
  padding-right: 5px;
  padding-bottom: 1px;
}
