@import 'themes';

$app_width: 700px;
$main_color: #a74e1b;
$second_color: #7d2304;

@mixin square_size($dimension) {
  width: $dimension;
  height: $dimension;
}

@mixin button_style() {
  @include theme(color, second_color);
  @include theme(background-color, button_bg);
  border: 2px solid;
  @include theme(border-color, main_color);
  cursor: pointer;
  padding-inline: 3px;
  text-align: center;
}

@mixin button_style_hover() {
  @include theme(background-color, button_bg_hover);
}

@mixin button_style_active() {
  @include theme(background-color, button_bg_active);
}
